body {
  min-width: 390px;
  margin: 0; 
  padding: 0;
  padding-top: 100px;
  background: #F2F4F5;
  background-image: url("https://images.unsplash.com/photo-1518655048521-f130df041f66?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8d29ya3NwYWNlfGVufDB8fDB8fA%3D%3D&w=1000&q=80");
  background-image: url("https://cdn.pixabay.com/photo/2017/05/19/06/22/desk-2325627__340.jpg");
  background-image: url("https://static.vecteezy.com/system/resources/thumbnails/003/655/529/small_2x/minimal-work-space-creative-flat-lay-of-workspace-desk-top-view-office-desk-with-keyboard-mouse-and-notebook-on-pastel-green-color-background-top-view-with-copy-space-flat-laygraphy-photo.jpg");


  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
  color: #1E1E1F;
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
}

#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}

@media only screen and (min-width: 600px) {
  body {
    padding-left: 15%;
    padding-right: 15%;
    padding-top: 5%;
  }
}
.TodoCounter {
    font-size: 28px;
    text-align: center;
    margin: 0;
    padding: 48px;
    color: #262d13;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
}

.TodoCounter--loading {
    opacity: 45%;
}
.todoSearch {
    background: #F9FBFC;
    border-radius: 2px;
    border: 2px solid #89b4ad;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  }
  
  .todoSearch::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .todoSearch::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .todoSearch:focus {
    outline-color: #61DAFA;
  }

  .todoSearch:disabled {
    opacity: 45%;
  }
.todoList ul {
    margin: 0;
    padding: 0 0 56px 0;
    list-style: none;
  }
.todoItem {
    background-color: #fffaf6;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  }
  
  .todoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    overflow: hidden;
    }
  .todoItem-p--complete {
    text-decoration: line-through;
  }
  
  .icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
  }
  
  .icon-check {
    position: absolute;
    left: 12px;
  }
  .icon-check--active {
    color: #4caf50;
  }
  
  .icon-delete {
    position: absolute;
    top: -24px;
    right: 0;
  }
  .icon-delete:hover {
    color: red;
  }
.LoadingTodo-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  }
  
  .LoadingTodo-text {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  
  .LoadingTodo-completeIcon,
  .LoadingTodo-deleteIcon {
    border-radius: 50px;
    height: 48px;
    width: 48px;
  }
  
  .LoadingTodo-completeIcon {
    position: absolute;
    left: 12px;
  }
  
  .LoadingTodo-deleteIcon {
    position: absolute;
    top: -24px;
    right: 0;
  }
  
  .LoadingTodo-container,
  .LoadingTodo-completeIcon,
  .LoadingTodo-deleteIcon {
    background: linear-gradient(90deg, rgba(250,250,250,1), rgb(200, 199, 199));
    background-size: 400% 400%;
    -webkit-animation: loadingAnimation 3s ease-in-out infinite;
            animation: loadingAnimation 3s ease-in-out infinite;
  }
  
  @-webkit-keyframes loadingAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  @keyframes loadingAnimation {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
form {
    width: 90%;
    max-width: 300px;
    background-color: #c9e7cd;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: #1E1E1F;
    margin-bottom: 26px;
  }
  
  textarea {
    background-color: #F9FBFC;
    border: 2px solid rgba(101,158,118,255);
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
  }
  
  textarea::-webkit-input-placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  textarea:focus {
    outline-color: #61DAFA;
  }
  
  .todoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .todoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
    border-radius: 10px;
    margin-top: 15px;
  }
  
  .todoForm-button--add {
    background: #cbe8d0;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    margin-left: 2px;
  }
  
  .todoForm-button--cancel {
    background: #f5cae5;
    margin-right: 2px;;
    
    
  }
.createTodoButton {
    background-color: hotpink;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 44px;
    right: 34px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
  
    -webkit-transform: rotate(0);
  
            transform: rotate(0);
    transition: .3s ease;
    z-index: 1;
  }
  
  .createTodoButton:hover {
    -webkit-transform: rotate(224deg);
            transform: rotate(224deg);
  }

  @media only screen and (min-width: 850px) {
    .createTodoButton {
      background-color: #61DAFA;
      height: 94px;
      width: 94px;
      right: 54px;
      bottom: 64px;

    }
  }
.modalBackground {
    background: rgba(253,248,192, 0.6);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
.ChangeAlert-bg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #1a470950;
    z-index: 2;
  }
  
  .ChangeAlert-container {
    position: fixed;
    bottom: -10px;
    left: -10px;
    right: -10px;
    padding: 34px;
    background-color: #1a512e;
    ;
    color: #f2f4f5;
    z-index: 2;
    text-align: center;
    font-size: 20px;
  }
  
  .ChangeAlert-container button {
    margin-top: 16px;
    margin-bottom: 16px;
    background-color: #c6dc93;
    width: 60px;
    height: 40px;
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(250, 250, 250, 0.55);
    /* font-family: "Monserrat"; */
    font-size: 18px;
    border-radius: 1%;
    border-style: none;
  }

